@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;
  max-width: 1024px;
  margin: 0 auto; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

@keyframes fade-from-left {
  from {
    opacity: 0;
    transform: translateX(-15%); }
  to {
    transform: none; } }

@keyframes fade-from-up {
  from {
    opacity: 0;
    transform: translateY(-15%); }
  to {
    transform: none; } }

@keyframes fade-from-right {
  from {
    opacity: 0;
    transform: translateX(15%); }
  to {
    transform: none; } }

@keyframes pulse {
  70% {
    transform: scale(1); }
  85% {
    transform: scale(1.07); }
  100% {
    transform: scale(1); } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes jump {
  90% {
    transform: translateY(0); }
  95% {
    transform: translateY(-5%); }
  100% {
    transform: translateY(0); } }

.header {
  min-height: 74px; }
  .header--active {
    position: relative; }

.header__link {
  display: flex;
  justify-content: center;
  position: absolute;
  z-index: 11;
  top: 25px;
  left: 35px;
  width: 192px;
  height: 24px;
  transform: none;
  transition: all 1s; }
  .header__link--active {
    top: 50vh;
    left: 50%;
    width: 800px;
    height: 100px;
    transform: translate(-50%, -50%);
    transition: none; }
    @media (max-width: 1023px) {
      .header__link--active {
        width: 550px;
        height: 70px; } }
  .header__link svg {
    width: 100%;
    height: 100%;
    overflow: visible; }

.header-icon-g-1,
.header-icon-g-2,
.header-icon-g-3 {
  animation-duration: 1.5s; }

.header-icon-g-1 {
  animation-name: fade-from-left; }

.header-icon-g-2 {
  animation-name: fade-from-up; }

.header-icon-g-3 {
  animation-name: fade-from-right; }

.loader {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: fixed;
  padding: 78px 112px;
  background-color: #ffffff;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.3s, z-index 0s 1s;
  opacity: 0;
  z-index: -1; }
  .loader--show {
    opacity: 1;
    z-index: 10; }

.loader__text {
  margin: 0;
  text-align: center;
  font-size: 24px;
  line-height: 22px;
  animation-name: pulse;
  animation-duration: 5s;
  animation-iteration-count: infinite; }

@media (max-width: 1023px) {
  .loader__text-desktop {
    display: none; } }

.loader__text-tablet {
  display: none; }
  @media (max-width: 1023px) {
    .loader__text-tablet {
      display: block; } }

.loader__text-desktop,
.loader__text-tablet {
  animation-name: fade-in;
  animation-duration: 1.5s; }

.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 35px;
  opacity: 0;
  clip-path: inset(0 100% 0 0);
  transition: none; }
  .main--loaded {
    opacity: 1;
    clip-path: inset(0 0 0 0);
    transition: opacity 0.5s ease-in-out 0.3s, clip-path 1s ease-in-out 0.3s, -webkit-clip-path 1s ease-in-out 0.3s; }

.main__title {
  width: 58.2%;
  margin: 0;
  font-size: 96px;
  line-height: 96px;
  color: #2c39f2; }
  @media (max-width: 1023px) {
    .main__title {
      width: 100%; } }

.main__block {
  width: 36.5%; }
  @media (max-width: 1023px) {
    .main__block {
      position: absolute;
      top: 0;
      left: -100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      height: 100%;
      background-color: #2c39f2;
      transition: left 0.5s; } }

.main--active .main__block {
  left: 0; }

.main__block-img {
  width: 316px;
  height: 289px;
  margin-bottom: 35px; }
  @media (max-width: 1023px) {
    .main__block-img .lightning path {
      fill: #ffffff; } }
  .main__block-img .hero {
    animation-name: jump;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-delay: 0s; }

.main__block-text {
  font-size: 30px;
  line-height: 40px; }
  @media (max-width: 1023px) {
    .main__block-text {
      max-width: 352px;
      color: #ffffff; } }

.main__bg {
  position: absolute;
  top: 43px;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1; }
  @media (max-width: 1023px) {
    .main__bg {
      display: none; } }

